// Square to be a function component

function Square(props) {
  return (
    <button className="square" onClick={props.onClick}>
      {props.value}
    </button>
  );
}

// Since the Square components no longer maintain state, the Square components receive values from the Board component
// and inform the Board component when they’re clicked. In React terms, the Square components are now
// controlled components. The Board has full control over them.

class Board extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      squares: Array(9).fill(null),
      xIsNext: true
    };
  }

  handleClick(i) {
    const squares = this.state.squares.slice(); //[MDN] The slice() method returns a shallow copy of a portion of an ARRAY into a new array object selected from start to end (end not included) where start and end represent the index of items in that array. The original array will not be modified.
    // slice() to create a copy of the squares array to modify instead of modifying the existing array

    if (calculateWinner(squares) || squares[i]) {
      return;
    }
    squares[i] = this.state.xIsNext ? "X" : "O";
    this.setState({
      squares: squares,
      xIsNext: !this.state.xIsNext
    });
  }

  renderSquare(i) {
    return (
      <Square
        value={this.state.squares[i]}
        onClick={() => this.handleClick(i)}
      />
    );
  }

  render() {
    const winner = calculateWinner(this.state.squares);
    let status;
    if (winner) {
      status = "Winner: " + winner;
    } else {
      status = "Next player:" + (this.state.xIsNext ? "X" : "O");
    }

    //const status = 'Next player:' + (this.state.xIsNext ? 'X' : 'O');

    return (
      <div>
        <div className="status">{status}</div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
      </div>
    );
  }
}

class Game extends React.Component {
  render() {
    return (
      <div className="game">
        <div className="game-board">
          <Board />
        </div>
        <div className="game-info">
          <div>{/* status */}</div>
          <ol>{/* TODO */}</ol>
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(<Game />, document.getElementById("root"));

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6]
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}

// There are generally two approaches to changing data.
// The first approach is to mutate the data by directly changing the data’s values.
// The second approach is to replace the data with a new copy which has the desired changes.

// Data Change with Mutation

// var player = {score: 1, name: 'Jeff'};
// player.score = 2;
// // Now player is {score: 2, name: 'Jeff'}

// Data Change without Mutation

// var player = {score: 1, name: 'Jeff'};
// var newPlayer = Object.assign({}, player, {score: 2});
// // Now player is unchanged, but newPlayer is {score: 2, name: 'Jeff'}

// // Or if you are using object spread syntax proposal, you can write:
// // var newPlayer = {...player, score: 2};

// SEVERAL BENEFITS

// Complex Features Become Simple :

// Immutability makes complex features much easier to implement
// Avoiding direct data mutation lets us keep previous versions  and reuse them later

// Detecting Changes:

// Detecting changes in MUTABLE objects is difficult because they are modified directly
// This detection requires the mutable object to be compared to previous copies of itself and the entire object tree to be traversed.
// Detecting changes in IMMUTABLE objects is considerably easier
// If the immutable object that is being referenced is different than the previous one, then the object has changed.

// Determining When to Re-Render in React :
// The main benefit of immutability is that it helps you build pure components in React.
// Immutable data can easily determine if changes have been made, which helps to determine when a component requires re-rendering
// You can learn more about shouldComponentUpdate() and how you can build pure components
